'use client'

import { useState, useMemo } from 'react'
import PropertyCard from '@/components/PropertyCard'
import PropertyFilter, { FilterState } from '@/components/PropertyFilter'
import SearchBar from '@/components/SearchBar'

interface Property {
  id: string
  title: string
  price: string
  location: string
  bedrooms: number
  bathrooms: number
  area: string
  type: string
  status: 'For Sale' | 'For Rent' | 'Sold'
  description: string
  images?: string[]
}

const propertiesData: Property[] = [
  {
    id: '1',
    title: 'Modern Family Home',
    price: '$450,000',
    location: '123 Main St, Springfield',
    bedrooms: 4,
    bathrooms: 3,
    area: '2,500 sqft',
    type: 'House',
    status: 'For Sale',
    description: 'Beautiful modern family home with spacious backyard and updated kitchen.',
    images: [
      '/file.svg',
      '/globe.svg',
      '/window.svg'
    ]
  },
  {
    id: '2',
    title: 'Downtown Apartment',
    price: '$1,800/mo',
    location: '456 Elm St, Metropolis',
    bedrooms: 2,
    bathrooms: 2,
    area: '1,200 sqft',
    type: 'Apartment',
    status: 'For Rent',
    description: 'Convenient downtown apartment with great city views and amenities.',
    images: [
      '/next.svg',
      '/vercel.svg'
    ]
  },
  {
    id: '3',
    title: 'Luxury Condo',
    price: '$750,000',
    location: '789 Oak St, Gotham',
    bedrooms: 3,
    bathrooms: 2,
    area: '1,800 sqft',
    type: 'Condo',
    status: 'Sold',
    description: 'Luxury condo with modern finishes and access to private gym and pool.'
  }
]

export default function RealEstatePage() {
  const [filters, setFilters] = useState<FilterState>({
    priceRange: [0, 1000000],
    propertyType: '',
    bedrooms: '',
    bathrooms: '',
    location: '',
    status: ''
  })

  const [searchTerm, setSearchTerm] = useState('')
  const [searchType, setSearchType] = useState('all')

  const handleFilterChange = (newFilters: FilterState) => {
    setFilters(newFilters)
  }

  const handleSearch = (term: string, type: string) => {
    setSearchTerm(term.toLowerCase())
    setSearchType(type)
  }

  const filteredProperties = useMemo(() => {
    return propertiesData.filter((property) => {
      // Filter by price range
      const priceNumber = Number(property.price.replace(/[^0-9.-]+/g,""))
      if (priceNumber < filters.priceRange[0] || priceNumber > filters.priceRange[1]) {
        return false
      }

      // Filter by property type
      if (filters.propertyType && property.type.toLowerCase() !== filters.propertyType.toLowerCase()) {
        return false
      }

      // Filter by bedrooms
      if (filters.bedrooms && property.bedrooms < Number(filters.bedrooms)) {
        return false
      }

      // Filter by bathrooms
      if (filters.bathrooms && property.bathrooms < Number(filters.bathrooms)) {
        return false
      }

      // Filter by status
      if (filters.status && property.status !== filters.status) {
        return false
      }

      // Filter by location
      if (filters.location && !property.location.toLowerCase().includes(filters.location.toLowerCase())) {
        return false
      }

      // Search term filtering
      if (searchTerm) {
        if (searchType === 'all') {
          const combined = `${property.title} ${property.location} ${property.type} ${property.description}`.toLowerCase()
          if (!combined.includes(searchTerm)) {
            return false
          }
        } else if (searchType === 'sale' && property.status !== 'For Sale') {
          return false
        } else if (searchType === 'rent' && property.status !== 'For Rent') {
          return false
        } else if (searchType === 'location' && !property.location.toLowerCase().includes(searchTerm)) {
          return false
        } else if (searchType === 'type' && !property.type.toLowerCase().includes(searchTerm)) {
          return false
        }
      }

      return true
    })
  }, [filters, searchTerm, searchType])

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <h1 className="text-4xl font-bold mb-8 text-center text-black">Property Listings</h1>

      <SearchBar onSearch={handleSearch} />

      <div className="mt-8 grid grid-cols-1 md:grid-cols-4 gap-8">
        <div className="md:col-span-1">
          <PropertyFilter onFilterChange={handleFilterChange} />
        </div>
        <div className="md:col-span-3 space-y-6">
          {filteredProperties.length > 0 ? (
            filteredProperties.map((property) => (
              <PropertyCard key={property.id} {...property} />
            ))
          ) : (
            <p className="text-center text-gray-600">No properties found matching your criteria.</p>
          )}
        </div>
      </div>
    </div>
  )
}
